﻿
.dashboardLowerPanel {
    background-color: #d9edf7;
}

.btn-save-paperless {
    float: right;
    background: #2eb134;
    border-color: #2eb134;
    margin: 0px 188px 0px 3px;
    color: #fff;
    border-radius: 5px;
    border-bottom-right-radius: 0;
    border-top-right-radius: 0;
}

#EcommOrEbillSuccessPanel {
    position: absolute;
    width: 72%;
}


.thumbnail-container-height > .thumbnail {
    min-height: 300px;
    position: relative;

    .caption {
        width: 100%;
        bottom: 0;
        position: absolute;
        background: #fff;
    }
}


.thumbnail {
    margin-top: 40px;
    margin-bottom: 40px;
    border: 0;
    border-radius: 0;
    padding: 0;
    background-color: #fff;

    > img {
        width: 100%;
    }

    h4 {
        font-family: "Open Sans", "Segoe UI", Tahoma, sans-serif;
        font-size: 16px;
        font-weight: 600;
        text-align: center;
    }

    .btn {
        text-transform: uppercase;
    }

    p {
        font-size: 12px;
        font-family: "Open Sans", "Segoe UI", Tahoma, sans-serif;
        color: #2F2F2F;
        text-align: center;
    }

    h1, .btn-link {
        color: #5CB85C;
    }
}
