﻿
.myAccount {
    margin-bottom: 20px;

    .myAccount-side-panel {

        h1 {
            color: rgba(6,81,128,1.0);
            font-size: 36px;
            font-weight: 400;
        }
        /* Take out default blue color for anchor tags. */
        a {
            color: #000;
        }

        .active {
            border-color: rgba(6,81,128,1.0);
        }

        li:hover {
            background-color: lighten($account-base, 70%);

            a, a:link, a:visited {
                color: $account-base;
            }
        }

        li.active a {
            color: $account-base;
        }

        li.active:before {
            color: $account-base;
        }
    }

    .list-group-item {
        border-width: 0px 0px 1px 0px;
        font-size: 14px;
        border-radius: 0;
        margin-bottom: 1px;

        &.active {
            background: #ffffff;
            border-color: #ddd;
        }

        &.active:before {
            content: "\F054";
            font-family: FontAwesome;
            display: inline-block;
            margin-left: -15px;
            margin-right: 6px;
        }
    }

    .message-center {

        h1, h2 {
            color: $account-base;
        }

        .account-info {
            p {
                background-color: #000;
                color: #fff;
                padding: 10px;
                display: inline-block;
            }
        }

        .profile-inline {
            h4 {
                display: inline;
            }
        }

        .control-value {
            border-color: transparent;
            box-shadow: none;
        }

        .control-label {
            font-weight: 700;
            text-align: right;
        }
    }
}


.my-account-profile {
    /* use these styles to set the pencil font icon next to update */
    $btn-Primarybg: rgba(46,177,52,1.0) !default;
    $btn-Primaryborder: darken($btn-Primarybg, 15%) !default;

    .invalid-feedback {
        margin-top: 0px;
        background: #d9edf7;
    }

    .btn-primary.btnUpdate,
    .btn-primary.btnUpdate:focus {
        background-color: transparent;
        border-color: transparent;
        box-shadow: none !important;
        outline: 0;
    }

    .btn-primary.btnUpdate:active:focus {
        color: #fff;
        background-color: $btn-Primaryborder;
        border-color: $btn-Primaryborder;
        border-style: outset;
        box-shadow: none !important;
        outline: 0;
    }
    /* use these styles to set the pencil font icon next to update */
    .col-form-label {
        font-weight: 500;
        text-align: right;
    }

    .btn-link {
        color: $payment-base;
        position: absolute;
        right: 10px;
        top: 0;
        text-indent: -300px;
        overflow: hidden;

        &:after {
            font-family: "FontAwesome";
            content: "\F040";
            display: inline-block;
            top: 5px;
            text-indent: 0;
            position: absolute;
            right: 9px;
            color: $payment-base;
        }
    }

    .error {
        color: red;
    }

    .update-panel {
        background-color: #d9edf7
    }

    .emailPanel {
        background-color: #eeeeee;
        padding-bottom: 20px;
        margin: 0 -4rem;
    }
}

.paperless-status {
    padding: 30px 0;
    margin-bottom: 83px;

    h2, h4 {
        color: black;
        font-weight: 400;
    }

    .status {
        color: #ffffff;
        display: inline-block;
        border-radius: 100px;
        height: 210px;
        width: 210px;
        text-align: center;
        background-image: url('../../../images/profile/circle_tree.png');
        background-repeat: no-repeat;

        h3 {
            position: absolute;
            bottom: 55px;
            width: 210px;
            text-shadow: 1px 1px 2px green;
        }

        h3.go {
            bottom: 90px;
        }

        small {
            margin-top: 30%;
            font-size: 20px;
            width: 90%;
            margin: 0 auto;
            color: #fff;
        }
    }

    .row.form-control {
        // margin-bottom:20px;
        padding-left: 0px;
        background-color: rgba(255,255,255,0.3);
        border: none;
        // height: auto;
    }

    h4 small {
        position: relative;
    }


    .input-group-addon {
        padding: 6px 12px;
        font-size: 14px;
        font-weight: 400;
        color: #555;
        text-align: center;
        background-color: #eee;
        border: 1px solid #ccc;
        border-radius: 4px;
        border-bottom-right-radius: 0;
        border-top-right-radius: 0;
    }

    .form-control, .input-group-addon {
        background-color: #2eb134;
        border-color: #2eb134;
        color: #fff;
    }
}

.switch-to-paperless {
    background-color: #fff;
    margin-top: 20px;
    padding-bottom: 20px;
    padding-top: 20px;

    .terms {
        background-color: #A9A9A9;
        padding: 20px;
    }

    .input-group-addon, .form-control {
        background-color: $payment-base;
        border-color: $payment-base;
        color: white;
    }
}

.tooltipStyle {
    background: transparent;
    border: 0px transparent;
}


.disableLink {
    pointer-events: none;
    color: lightgrey;
}

@media (max-width: 768px) {
    .my-account-profile {
        border: none;
        padding: 0;
    }
}

.jumbotron {
    background-color: transparent;
}
