  margin-bottom: 0;
  background-color: #222;
  border-color: #090909; }
  .header-status-nav .nav-item {
    min-height: 32px; }
  .header-status-nav .nav-link {
    text-align: center;
    line-height: 32px;
    height: 32px; }
  .header-status-nav .fa-white {
    color: #ffffff; }
  .header-status-nav .navbar-nav > .nav-item > .nav-link {
    color: white;
    padding-left: 10px;
    padding-right: 10px; }
  .header-status-nav .navbar-nav > .nav-item > a:hover, .header-status-nav .navbar-nav > .nav-item > a:focus {
    text-decoration: none;
    background-color: #2e2e2e; }

