﻿.energyinsights {
    
    .energyinsights-side-panel {
        h1 {
            color: $control-base;
            font-size: 36px;
            font-weight: 400;
        }

        .active {
            border-color: rgba(6,81,128,1.0);
        }

        li:hover {
            background-color: lighten($control-base, 55%);

            a, a:link, a:visited {
                color: $control-base;
            }
        }

        li.active a {
            color: $control-base;
        }

        li.active:before {
            color: $control-base;
            border-bottom-color: $control-base;
        }
    }

    .list-group-item {
        border-width: 0px 0px 1px 0px;
        font-size: 14px;
        border-radius: 0;
        margin-bottom: 1px;

        &.active {
            background: #ffffff;
            border-color: #ddd;
        }

        &.active:before {
            content: "\F054";
            font-family: FontAwesome;
            display: inline-block;
            margin-left: -15px;
            margin-right: 6px;
        }
    }

    .control-profile, .callout-container.control {
        h1, h2, h3, h4 {
            color: $control-base;
            font-weight:400;
        }
    }

    .control-profile {
        position: static;
    }

    .container-breakout {
        margin-top: 30px;
        padding: 40px 0;

        h4 {
            color: #000000;
        }
    }

    .container-breakout {
        margin-top: 30px;
        padding: 40px 0;
        background-color: rgba(216,238,206,0.9);
    }

    .container-breakout h4 {
        color: #000000;
    }

    .energyinsights-usage-history {
    }

    .d-flex[style*="display:none"], .d-flex[style*="display: none"] {
        display: none !important;
    }
}

.container-breakout {
    background-color: #d9edf7;

    h4 {
        color: #000000;
    }

    tr.options {
        button {
            outline: none !important;
            padding: 10px 0;
        }
    }

    .btn-link {
        font-weight: 400;
        color: #000;
        font-size: 14px;
    }

    .options-container {
        text-align: center;
        margin-top: 20px;
        margin-bottom: 20px
    }

    tr.bill {

        td {
            vertical-align: middle !important;
        }

        button,
        td > div {
            padding: 10px 0;
        }
    }
}


.energy-savings {
    h1 {
        font-weight: 400;
    }

    .panel-group .panel {
        margin-bottom: 0;
        border-radius: 4px;
    }

    .panel {
        box-shadow: 0 1px 1px rgba(0,0,0,.05);
    }

    .panel-heading {
        padding: 10px 15px;
        border-top-right-radius: 3px;
        border-top-left-radius: 3px;
    }

    .panel-title {
        margin: 0px;
        font-weight: 400;
        font-size: 16px;
    }

    .panel-group .panel + .panel {
        margin-top: 5px;
    }

    .control-profile {
        position: static;
    }

    h1, .h1 {
        font-size: 36px;
    }

    .h3, h3 {
        font-size: 24px;
    }

    .panel-default {
        background: none;
        border: none;

        h5 a {
            color: $account-base;

            &:hover {
                border-bottom: 1px solid $account-base;
                ;
            }
        }
    }

    .panel-default > .panel-heading {
        background-color: transparent;
        border-color: transparent;
    }

    .masonry-brick {
        margin-bottom: 20px;

        .img-responsive {
            border-top: 6px solid $payment-base;
        }

        h3 {
            margin-top: 10px;
            text-transform: uppercase;
            text-align: center;
            font-size: 24px;
        }
    }

    .col-lg-4 {
        h4 {
            margin-top: 10px;
            text-transform: uppercase;
            text-align: left;
            font-size: 18px;
        }

        hr {
            margin-bottom: 1px;
        }
    }

    .col-lg-8 {

        .fa-in-circle {
            background: #fea220;
            color: #fff;
            border-radius: 30px;
            display: inline-block;
            padding: 17px 0;
            width: 60px;
            height: 60px;
            text-align: center;
            margin: -30px auto 20px;
            display: block;
        }

        > .row {
            padding-right: 25px;
        }

        .row:nth-child(1) .masonry-brick {

            .img-responsive {
                border-top: 6px solid $account-base;
                width: 100%;
            }

            h3 {
                color: $account-base;
                font-weight: 400;
            }

            .fa-in-circle {
                background-color: $account-base;
            }

            .panel-title > a {
                color: $account-base;

                &:hover {
                    border-bottom: 1px solid $account-base;
                }
            }
        }


        .row:nth-child(2) .masonry-brick {

            .img-responsive {
                border-top: 6px solid $plans-base;
                width: 100%;
            }

            h3 {
                color: $plans-base;
                font-weight: 400;
            }

            .fa-in-circle {
                background-color: $plans-base;
            }

            .panel-title > a {
                color: $plans-base;

                &:hover {
                    border-bottom: 1px solid $plans-base;
                }
            }
        }

        .row:nth-child(3) .masonry-brick {

            .img-responsive {
                border-top: 6px solid #2eb134;
                width: 100%;
            }

            h3 {
                color: #2eb134;
                font-weight: 400;
            }

            .fa-in-circle {
                background-color: #2eb134;
            }

            .panel-title > a {
                color: #2eb134;

                &:hover {
                    border-bottom: 1px solid #2eb134;
                }
            }
        }
    }
}

