﻿.multi-account-modal-popup {

    .modal {
        padding: 0 !important;
    }

    .modal-dialog {
        height: 100%;
        padding: 0;
        // max-width: 1250px !important;
    }

    .modal-body {
        overflow: hidden;
        background: $multiAccountModalbg;
    }

    .modal-content {
        border-radius: 0;
        box-shadow: none;
        border: none;
    }

    .loading {
        p, .fa {
            color: #fff;
        }
    }

    h3 {
        margin-top: 0;
        color: #fff;
        font-size: 24px;
    }

    .account-header {
        .account-title {
            color: white;
            padding: 10px;
            text-align: center;
            font-size: 18px;
            margin-bottom: 0;
        }
    }

    h1, h2, h3, h4, h5 {
        font-weight: 400;
    }

    .h5, h5 {
        font-size: 0.875rem;
    }

    /*Style the DIV*/
    .select-account-container {
        margin: 10px 0;
        //padding: 5px;
        border: 1px solid #fff;
        text-align: center;
        background-color: rgba(255,255,255,.1);
        border-radius: 5px;
        padding: 10px;
        min-height: 175px;
    }

    /*Fade out the particular DIV when user hover on that DIV*/
    .select-account-container:hover {
        background: rgba( 255,255,1255,.9);
        cursor: pointer;
        color: $multiAccountHover !important;
        /*Opacity*/
        zoom: 1;
        opacity: 1;

        h3 {
            color: #fff;
        }

        .account-title {
            color: $multiAccountTitle;
        }
    }

    .continue-button {
        margin: 15px 25px 0 0;
    }

    a {
        cursor: pointer;
    }

    .tel {
        white-space: nowrap;
    }
}

@media (max-width: 992px) {

    .multi-account-modal-popup .modal-lg {
        width: 450px !important;
    }
}

@media (max-width: 768px) {

    .home-multi-accounts-modal .modal-lg {
        width: 350px !important;
    }
    .multi-account-modal-popup .modal-lg {
        width: 100% !important;
        padding-right:15px;
    }
}
