﻿.home-carousel {

    margin-bottom: 4rem;

    .carousel-indicators {
        position: absolute;
        bottom: -17px;
    }

    .carousel-item {
        height: 280px;

        img {
            /*height: 20em;*/
            width: 100%;
            height: 280px;
            max-height: 280px;
        }
    }

    span.carousel-control-prev-icon, span.carousel-control-next-icon {
        font-weight: bold;
        font-size: 60px !important;
        height: 25px;
        width: 25px;
    }

    .disableLink {
        pointer-events: none;
    }
}
