﻿.my-bill-grid {
    .btn-myAccountPrimary a:hover {
        color: white !important;
    }

    table {
        margin-bottom: 10px;
        margin-top: 0px;
    }

    tr.bg-success > td {
        border-top: 1px solid $myBillText;
        border-bottom: 1px solid $myBillText;
        padding-top: 0px;
        padding-bottom: 0px;
        background-color: #dff0d8;


        h4 {
            color: $myBillText;
            text-align: left;
        }
    }



    tr.bg-spacer > td {
        padding: 0;
    }

    .text-left {
        text-align: left;
    }

    .text-center {
        text-align: center;
    }

    .disconnected {
        color: red !important;
    }

    .exceededDueDatebtn {
        background-color: orange !important;
    }

    .exceededDueDate {
        color: orange !important;
    }

    .paymentMade {
        color: $myBillText;
    }

    .paymentButton {
        margin-bottom: 5px;
    }

    .btn-round-lg {
        border-radius: 5px;
    }

    .btn-round-sides {
        border-radius: 0px 5px 5px 0px;
    }

    .custom {
        width: 100% !important;
    }

    .linkLabel {
        margin: 15px 5px 5px 5px;
        font-weight: 700;
        text-align: center;
        font-size: 12px;
    }

    .btn-payment {
        /*margin: 5px 5px 5px 5px;*/
        font-weight: 700;
        text-align: center;
        padding-bottom: 5px;
    }

    .btn-payment:hover,
    .btn-payment.hover {
        color: black !important;
    }

    .btn-payment:active,
    .btn-payment.active {
        color: white !important;
    }

    .btn-link {
        color: $myBillText !important;
        box-shadow: none !important;
    }

    .btn-link:active {
        color: #5CB85C
    }

    .dropdown-item.active, .dropdown-item:active {
        background-color: #5CB85C;
        color: white;
    }

    .alert-info {
        max-width: 60%;
        padding: 2px 12px;
        font-size: 12px;
        text-align: left;
    }
}
