﻿.payments {
    margin-bottom: 20px;
    
    .payments-side-panel {
        h1 {
            color: $payment-base;
            font-size: 36px;
            font-weight: 400;
        }

        .active {
            border-color: rgba(6,81,128,1.0);
        }

        li:hover {
            background-color: lighten($payment-base, 55%);

            a, a:link, a:visited {
                color: $payment-base;
            }
        }

        li.active a {
            color: $payment-base;
        }

        li.active:before {
            color: $payment-base;
            border-bottom-color: $payment-base;
        }
    }

    .list-group-item {
        border-width: 0px 0px 1px 0px;
        font-size: 14px;
        border-radius: 0;
        margin-bottom: 1px;

        &.active {
            background: #ffffff;
            border-color: #ddd;
        }

        &.active:before {
            content: "\F054";
            font-family: FontAwesome;
            display: inline-block;
            margin-left: -15px;
            margin-right: 6px;
        }
    }

    .payment-profile, .callout-container.payment {
        h1, h2, h3, h4 {
            color: $payment-base;
            font-weight: 400;
        }

        .pay_ment {
            color: $paymeny_clr;
        }
    }

    .payment-profile {
        position: static;
    }

    .container-breakout {
        margin-top: 30px;
        padding: 40px 0;
        background-color: rgba(216,238,206,0.9);
    }

    .container-breakout h4 {
        color: #000000;
    }

    .viewMyBill {
        .your_settings {
            .btn{font-size:18px;color:black;text-transform:none;}
            .fa-check{ color: $payment-base !important;}
        }

        table {
            td {
                padding: 15px;
            }

            tr.bg-success > td {
                border-top: 1px solid $payment-base;
                border-bottom: 1px solid $payment-base;
                padding-top: 5px;
                padding-bottom: 5px;

                h4 {
                    color: $payment-base;
                    text-align: left;
                }
            }
            //tr.bg-success{
            //  td {
            //    line-height: 4rem;
            //        padding:5px 10px;
            //    margin-bottom: 5px;
            //  }
            //}
            tr.total {
                border-top: 1px solid #999;

                td {
                    padding-top: 20px;
                }
            }
        }

        .btn-round-lg {
            border-radius: 5px;
        }

        .btn-round-sides {
            border-radius: 0px 5px 5px 0px;
        }

        .custom {
            width: 300px !important;
        }

        .linkLabel {
            margin: 15px 5px 5px 5px;
            font-weight: 700;
            text-align: center;
            font-size: 12px;
        }

        .btn-payment {
            /*margin: 5px 5px 5px 5px;*/
            font-weight: 700;
            text-align: center;
            padding-bottom: 5px;
            /*color: #5cb85c !important;*/
        }

        .btn-payment:hover,
        .btn-payment.hover {
            color: black !important;
        }

        .btn-payment:active,
        .btn-payment.active {
            color: white !important;
        }

        .exceededDueDatebtn {
            background-color: orange !important;
        }

    }

    .makepayments {

        .embed-responsive-custom {
            padding-top: 100%;
            min-height: 100%;
            /*position: relative;
            overflow: hidden;
            padding-top: 99.25%;*/
        }

        .iframe-container {
            height: 0;
            overflow: auto;
            position: relative;
            padding-bottom: 100%;
            max-width: 100%;
            min-height: 100%;
        }

        .iframe-container iframe {
            border: 0;
            width: 100%;
            height: 100%;
            max-width: 100%;
            top: 0;
            left: 0;
            position: absolute;
        }
        /* 4x3 Aspect Ratio */
        .iframe-container-4x3 {
            padding-top: 75%;
        }

        iframe {
            width: 1px;
            min-width: 100%;
        }
    }

    .paymentAccounts {

        .embed-responsive-custom {
            padding-top: 100%;
            min-height: 100%;
            padding-bottom: 40%;
        }

        .iframe-container {
            height: 0;
            overflow: auto;
            position: relative;
            padding-bottom: 100%;
            max-width: 100%;
            min-height: 100%;
        }

        .iframe-container iframe {
            border: 0;
            width: 100%;
            height: 100%;
            max-width: 100%;
            top: 0;
            left: 0;
            position: absolute;
        }
        /* 4x3 Aspect Ratio */
        .iframe-container-4x3 {
            padding-top: 75%;
        }

        iframe {
            width: 1px;
            min-width: 100%;
        }
    }

    .paymentOptionsAutoPay {

        .embed-responsive-custom {
            padding-top: 100%;
            min-height: 100%;
            padding-bottom: 40%;
        }

        .iframe-container {
            height: 0;
            overflow: auto;
            position: relative;
            padding-bottom: 100%;
            max-width: 100%;
            min-height: 100%;
        }

        .iframe-container iframe {
            border: 0;
            width: 100%;
            height: 100%;
            max-width: 100%;
            top: 0;
            left: 0;
            position: absolute;
        }
        /* 4x3 Aspect Ratio */
        .iframe-container-4x3 {
            padding-top: 75%;
        }

        iframe {
            width: 1px;
            min-width: 100%;
        }
    }

    .paymentOptions {

        .payment-options {

            .payment-option {
                text-align: center;
                display: block;
                text-decoration: none;
                margin-bottom: 2rem;
                padding-top: 10px;
                padding-right: 10px;
                padding-bottom: 30px;
                padding-left: 10px;
                display: block;
                border: 1px solid transparent;
                opacity: .8;

                .fa {
                    color: $plans-base;
                }

                &.payment-option-active {
                    border: 1px solid $payment-base;
                    opacity: 1.0;
                }

                &.payment-option-active:after {
                    display: block;
                    position: absolute;
                    content: "";
                    height: 0;
                    bottom: -135px;
                    left: 12px;
                    border: 130px solid transparent;
                    border-top: 37px solid $payment-base;
                }

                h4 {
                    margin-top: 0;
                    min-height: 55px;
                }

                .h2, h2 {
                    font-size: 30px;
                }

                .payment-option-name {
                    margin-top: 20px;
                }

                .h4, h4 {
                    font-size: 18px;
                }
            }
        }
    }



    .paymentHistory {

        $mynewcolor: rgba(46,177,52,1.0) !default;

        .btn-primary {
            @include button-variant($mynewcolor, darken($mynewcolor, 7.5%), darken($mynewcolor, 10%), lighten($mynewcolor,5%), lighten($mynewcolor, 10%), darken($mynewcolor,30%));
        }

        tr.options {
            button {
                outline: none !important;
                padding: 10px 0;
            }
        }

        .btn-link {
            font-weight: 400;
            color: #000;
            font-size: 14px;
        }

        .options-container {
            text-align: center;
            margin-top: 20px;
            margin-bottom: 20px
        }

        tr.bill {

            td {
                vertical-align: middle !important;
            }

            button,
            td > div {
                padding: 10px 0;
            }
        }

        .my-pagination {
            text-align: center;
        }

        .pagination {
            display: flex;
            justify-content: center;
        }

        .pagination li {
            display: block;
        }

        .page-link {
            color: #333;
        }
    }
}
.mb_100 {
    margin-bottom: 80px;
}
.view-invoice-detail-modal {

    text-align: initial;

    .view-my-bill-header {
        .plan-title {
            display: inline-block;
        }

        .close-btn {
            font-size: 40px;
        }
    }

    h1, h2, h3, h4, h5 {
        font-weight: bold;
    }

    .modal {
        padding: 0 !important;
    }

    // testing
    .modal-dialog {
        // max-width: 600px !important;
        height: 100%;
        padding: 0;
        // max-width: 1250px !important;
    }

    .modal-content {
        border-radius: 0 !important;
    }

    .modal-header {
        border-bottom: 0px white;

        .modal-title {
            font: bold;
        }

        button.close {
            margin-top: 0px;
            padding-top: 10px;
        }
    }
}
.alert-success {
    color: black !important
}
.bill_foot {
    
    hr {
        margin: 10px 3px;
        border-color: #fff
    }

    h2 {
        margin-top: 0
    }

    .chart {
        font-family: "Oswald", sans-serif;
    }

    p {
        margin-bottom: 0;
        font-size: 12px
    }

    .chart path:hover {
        opacity: .65;
    }

    .gexa-row {
        padding: 5px 10px;
        background-color: rgba(46, 177, 52, 1.0);
        color: #fff;
    }

    .gexa-row.selected {
        background-color: rgba(46, 177, 52, 1.0);
        color: #fff;
    }

    .oncor-row {
        background-color: rgba(6, 81, 128, 1);
        padding: 5px 10px;
        color: #fff;
    }

    .oncor-row.selected {
        background-color: rgba(6, 81, 128, 1);
        padding: 5px 10px;
        color: #fff;
    }

    .taxes-row {
        background-color: rgba(254, 162, 32, 1);
        padding: 5px 10px;
        color: #fff;
    }

    .taxes-row.selected {
        background-color: rgba(254, 162, 32, 1);
        padding: 5px 10px;
        color: #fff;
    }

    .details {
        padding: 5px 10px;
    }

    .fa-plus {
        cursor: pointer;
        margin-top: 2px;
    }

    .details div[class*="col-"] {
        border-bottom: 1px solid #eee;
        padding-bottom: 3px;
    }

    .viewBillGaugeComponent {
        width: 270px;
        margin-bottom: 2rem;
        position: relative;
        border-radius: 151px;
        z-index: 22222;
        height: 270px;
        margin-left: auto;
        margin-right: auto;

        .gaugeChart {
            z-index: -1;
        }

        .gaugeText {
            position: absolute;
            text-align: center;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            height: 100%;
            width: 100%;
            z-index: -1;
        }
    }

    .toggle-container {
        overflow: hidden;
    }


    div.ng-trigger-openCloseTDUCharges, div.ng-trigger-openCloseTaxCharges, div.ng-trigger-openCloseGexaCharges {
        max-height: 500px; /* approximate max height */
        // transition-timing-function: ease-in;
        //transition-timing-function: cubic-bezier(0, 1, 0.8, 1);
        -webkit-transition: max-height 0.5s ease-in-out;
        -moz-transition: max-height 0.5s ease-in-out;
        -o-transition: max-height 0.5s ease-in-out;
        transition: max-height 0.5s ease-in-out;
    }
}

.view-invoice-detail-modal {
    hr.new1 {
        border: 1px solid $myBillText;
    }

    p {
        margin-bottom: 0;
        font-size: 12px
    }

    h2 {
        color: $myBillText;
        font-weight: 400;
    }

    .gexa-row {
        font-weight: 700;
        padding: 10px 0px;
    }

    .fa-plus {
        background: $myBillText;
        border-radius: 50%;
        color: white;
        width: 20px;
        height: 20px;
        padding: 4px 4px;
    }

    .MyBill {
        width: 230px;
        height: 230px;
        border-radius: 50%;
        border: 0px;
        background: $myBillText;
    }

    .fa-file-pdf-o {
        color: white;
        font-size: 40px;
    }

    .details {
        padding: 5px 0px;
    }
}